@import url("https://fonts.googleapis.com/css2?family=Courgette&family=Exo+2:wght@200;300;400;500&family=Lobster&family=Poppins:wght@100;200;300;400&display=swap");
.winning-popup {
  color: #fff;
  z-index: 9999999999;
  position: absolute;
  transform: translate(-50%, -50%) scale(0);
  top: 50%;
  left: 50%;
  font-size: 40px;
  color: #f7f7f7;
  padding: 41px 20px;
  /* max-width: 600px; */
  width: 100%;
  height: 100%;
  text-align: center;
  /* background-image: linear-gradient(90deg,rgba(235,41,88,0),#eb2958,rgba(235,41,88,0)); */
  animation: neon1 0.5s ease-in-out;
  /* background: rgb(9,9,121); */
  /* background: radial-gradient(
    circle,
    #eb2958 12%,
    rgba(235, 41, 89, 0.2) 52%,
    rgb(235 41 88 / 0%) 100%,
    rgba(235, 41, 89, 0) 100%
  ); */
  background: radial-gradient(
    circle,
    #77006c96 12%,
    rgba(235, 41, 89, 0.2) 52%,
    rgb(235 41 88 / 0%) 100%,
    rgba(235, 41, 89, 0) 100%
  );
  animation-fill-mode: forwards;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  flex-direction: column;
}
.win-case {
  background: radial-gradient(
    circle,
    #00774596 12%,
    rgb(3 111 3 / 29%) 52%,
    rgb(235 41 88 / 0%) 100%,
    rgba(235, 41, 89, 0) 100%
  );
}
.popup-wrapper {
  position: fixed;
  z-index: 999999;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  left: 50%;
  top: 50%;
  height: 100%;
  width: 100%;
}
.popup-wrapper:before {
  content: "";
  position: absolute;
  background: hsl(0deg 0% 0% / 84%);
  height: 100%;
  width: 100%;
  z-index: 999999;
}
@keyframes neon1 {
  from {
    transform: translate(-50%, -50%) scale(0);
  }
  to {
    transform: translate(-50%, -50%) scale(1);
  }
}
.winning-popup p {
  margin: 0;
  font-weight: 500;
  font-size: 45px;
  font-family: "Courgette", cursive;
  font-family: "Exo 2", sans-serif;
  font-family: "Lobster", cursive;
}

.winning-popup h6,
.winning-popup h1 {
  margin: 0;
  font-size: 55px;
  font-family: "Courgette", cursive;
  font-family: "Exo 2", sans-serif;
  font-family: "Lobster", cursive;
  text-shadow: 0px 6px 0px black;
}
.winning-popup h1 {
  margin-top: 0px;
}
.winning-popup h6 {
  font-size: 140px;
  color: #ffeb00;
  font-weight: 700;
}

.coins:before {
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  background: red;
  background-position: center;
  background: url("../../assets/images/tower/car-ray.png");
  left: 0px;
  background-position: center;
  background-size: 100%;
  background-repeat: no-repeat;
  z-index: -1;
  animation: bubble 20s infinite;
}
@keyframes bubble {
  0% {
    transform: rotate(0deg);
    opacity: 0.8;
  }

  50% {
    transform: rotate(180deg);
    opacity: 1;
  }

  100% {
    transform: rotate(360deg);
    opacity: 0.8;
  }
}
.coins-img img {
  height: 130px;
  margin-top: -39px;
  z-index: -1;
}
.coins {
  opacity: 0.3;
  max-width: 900px;
  width: 100%;
  margin: 0px auto;
  max-height: 727px;
  height: 100%;
  position: absolute;
}

/******** deposit popup ************/
.deposit-popup .modal-dialog {
