  color: rgb(180, 157, 99);
  font-weight: bold;
}

.deposit-giftcard-popup .gift-left {
  padding: 20px 0px 20px 20px;
  z-index: 99;
}
.deposit-giftcard-popup .gift-box::before {
  background: url(../../assets/images/deposit/gift-box.png);
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  z-index: 9;
}
